import Table from '../table/Table';
import	billsData	from '../data/bills.json';
import {getCustomerslist,getOrderslist, createPdf}	from '../../api/Common';
function Bills() {
	const orders = JSON.parse(getOrderslist())
	let list = []
	let data = []
	orders.forEach(element => {
		console.log(orders)
		if(element["memory"]=="1"){
			if(!list[element["order_id"]]){
				console.log("here nowwwww")
				list[element["order_id"]] = []
				list[element["order_id"]].order_id = element["order_id"]
				list[element["order_id"]].delivered = 0
				list[element["order_id"]].awaiting_pickup = 0
				list[element["order_id"]].in_delivery = 0
				list[element["order_id"]].postponed = 0
				list[element["order_id"]].in_storage = 0
				list[element["order_id"]].awaiting_return = 0
				list[element["order_id"]].in_storage_to_return = 0
				list[element["order_id"]].return_postponed = 0
				list[element["order_id"]].return_pickup_postponed = 0
				list[element["order_id"]].returned = 0
				list[element["order_id"]].total_packages = 0
				list[element["order_id"]].total_weight = 0
				list[element["order_id"]].total_price = 0
				list[element["order_id"]].package_number = 0
				console.log(list)
			}
			console.log(list.length)
			for (let j=0; j<list.length; j++){
				if(element["order_id"]==list[j]["order_id"]){

					switch (element["status"]) {
						case "livré":
							list[j].delivered = list[j].delivered + 1
							break;
						case "en attente d'enlèvement":
							list[j].awaiting_pickup = list[j].awaiting_pickup + 1
							break;
						case "en cours de livraison":
							list[j].in_delivery = list[j].in_delivery + 1
							break;
						case "Livraison reportée":
							list[j].postponed = list[j].postponed + 1
							break;
						case "en entrepôt":
							list[j].in_storage = list[j].in_storage + 1
							break;
						case "en attente de retour":
							list[j].awaiting_return = list[j].awaiting_return + 1
							break;
						case "en entrepôt (retour)":
							list[j].in_storage_to_return = list[j].in_storage_to_return + 1
							break;
						case "retour reporté":
							list[j].return_postponed = list[j].return_postponed + 1
							break;
						case "enlèvement reporté (retour)":
							list[j].return_pickup_postponed = list[j].return_pickup_postponed + 1
							break;
						case "retourné":
							list[j].returned = list[j].returned + 1
							break;
						default:
						console.log("Status not recognized");
					}

					list[j].total_packages = list[j].total_packages + 1
					list[j].total_weight = element["weight_g"] + list[j].total_weight
					list[j].total_price = element["price_base"] + list[j].total_price
					list[j].package_number = element["package_number"] + list[j].package_number
				}
			}
		}
	});
	for (let k in list){
		data.push(list[k])
	}
	console.log(data)
	return(
		<>
		<Table data={data} actions={billsData["actions"]}	columns={billsData["columns"]} title={billsData["title"]} crumbs_list={billsData["crumbs"]} ></Table>
		</>
	);
}

export default Bills;
